import React, { useState, useEffect } from "react";
import { Row, Col } from "react-bootstrap";
import { Neomarphismui } from "../Neomorphism";

const Stats = (props) => {
  const [data, setData] = useState(props.data);
  const [confirmed, setConfirmed] = useState(0);
  const [active, setActive] = useState(0);
  const [recoveries, setRecoveries] = useState(0);
  const [deaths, setDeaths] = useState(0);
  const [deltas, setDeltas] = useState(0);

  useEffect(() => {
    setData(props.data);
  }, [props.data]);

  useEffect(() => {
    const parseData = () => {
      let confirmed = 0;
      let active = 0;
      let recoveries = 0;
      let deaths = 0;
      let deltas = {};
      data.forEach((state, index) => {
        if (index !== 0) {
          confirmed += parseInt(state.confirmed);
          active += parseInt(state.active);
          recoveries += parseInt(state.recovered);
          deaths += parseInt(state.deaths);
        } else {
          deltas = {
            confirmed: parseInt(state.deltaconfirmed),
            deaths: parseInt(state.deltadeaths),
            recovered: parseInt(state.deltarecovered),
          };
        }
      });
      setConfirmed(confirmed);
      setActive(active);
      setRecoveries(recoveries);
      setDeaths(deaths);
      setDeltas(deltas);
    };
    parseData();
  }, [data]);

  return (
    <Row>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Confirmed"
          color="neumorphic variation2 brown"
          data={confirmed}
          inc={
            deltas
              ? deltas.confirmed >= 0
                ? "+" + deltas.confirmed
                : "+0"
              : ""
          }
        />
      </Col>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Active"
          color="neumorphic variation2 orange"
          data={active}
          inc={""}
        />
      </Col>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Recovered"
          color="neumorphic variation2 green"
          data={recoveries}
          inc={
            deltas
              ? deltas.recovered >= 0
                ? "+" + deltas.recovered
                : "+0"
              : ""
          }
        />
      </Col>
      <Col xs={6} sm={6} md={3} lg={3}>
        <Neomarphismui
          title="Death"
          color="neumorphic variation2 red"
          data={deaths}
          inc={deltas ? (deltas.deaths >= 0 ? "+" + deltas.deaths : "+0") : ""}
        />
      </Col>
    </Row>
  );
};

export default Stats;
